import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select } from 'antd';
import { typeOfRewards } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    level: '',
    type: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

const { Option } = Select;

@Form.create({ name: 'formTypeOfRewards' })
class FormTypeOfRewardsPage extends Component {
  state = {
    typeOfRewardsId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ typeOfRewardsId: match.params.id });
      this.getTypeOfRewards(match.params.id);
    }
  };

  getTypeOfRewards = code => {
    const typeMap = typeOfRewards.filter(object => object.type === code)[0];
    const initialValue = handleInitialValue(typeMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { typeOfRewardsId } = this.state;
    return (
      <Card title={typeOfRewardsId ? 'Cập nhật loại khen thưởng' : 'Thêm loại khen thưởng'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Cấp khen thưởng" {...formItemLayout}>
              {getFieldDecorator('level', {
                initialValue: initialValue.level,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Loại khen thưởng" hasFeedback>
              {getFieldDecorator('type', {
                initialValue: initialValue.type,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card >
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTypeOfRewardsPage {...props} />
  </Page>
));



